  margin-left: calc(0.5rem * calc(1 - var(--tw-space-x-reverse))) !important;
}

.amount-number {
  --tw-text-opacity: 1 !important;
  color: rgb(245 158 11 / var(--tw-text-opacity)) !important;
}

.relay-settings svg.enabled {
  --tw-text-opacity: 1 !important;
  color: rgb(245 158 11 / var(--tw-text-opacity)) !important;
}
